.container{
    display:flex;
    padding:7%;
    flex-wrap: wrap;
    justify-content: center;
}

.boxes{
    width: 500px;
    display:flex;
    padding:1%;
}

.small-box h1, .small-box p, .small-box button{
    padding-bottom: 1%;
    margin-left:3%;
}

input{
    padding:2%;
}

.inputparent{
    width:100%;
    display: flex;
    justify-content: center;
    align-items:end;
    height:8vh;
}

button{
    padding:1% 2%;
    border:1px solid black
}

@media(max-width:980px){
    .container{
        zoom:200%;
    }
    input{
        zoom:200%;
    }
    .inputparent{
        height:12vh
    }
}